@media screen and (min-width: 320px) and (max-width: 767px) {
    .container {
        /*width: 100%;*/
        /*height: fit-content;*/

        padding-bottom: 25px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .topSide {
        height: fit-content;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: space-between;
        gap: 15px;
    }

    .firstBtn {
        font-size: 18px;
        padding: 12px 20px;
        background: transparent;
        border: 1px solid var(--black);
        transition: all 0.3s linear;
        border-radius: 5px;
        color:var(--black) ;
    }

    .firstBtn:hover {
        background: var(--black);
        color: white;
        transition: all 0.3s linear;
    }

    .secondBtn {
        width: 200px;
        font-size: 18px;
        padding: 12px 20px;
        background: var(--main-color);
        color: white;
        transition: all 0.3s linear;
        border-radius: 5px;
        margin-bottom: 15px;
    }

    .secondBtn:hover {
        background: var(--black);
        color: white;
        transition: all 0.3s linear;
    }

    .info {

    }

    .info a {
        display: block;
        width: 100%;
        font-size: 25px;
        font-weight: 800;
        color: var(--black);
        letter-spacing: 0.05em;
        text-align: center;
        margin-bottom: 10px;
    }

    .info p {
        font-size: 16px;
        font-weight: 300;
        text-align: center;
    }

    .nav {



    }

    .navList {
        display: flex;
        justify-content: center;
        flex-direction: column;
        gap: 20px;
        padding-bottom: 25px;
    }

    .navList li {

    }

    .icon {
        width: 50px;
        height: 50px;
        margin: 25px;
        fill: var(--black);
    }


    .navLink {
        font-size: 18px;
        line-height: 24px;
        color: var(--black);
        transition: all 0.2s linear;
    }

    .navLink:hover {
        color: var(--main-color);
        transition: all 0.2s linear;
    }
}

