@media screen and (min-width: 320px) and (max-width: 767px) {
    .container{
        width: 100%;
        height: fit-content;
        display: flex;
        flex-direction: column;
        padding: 35px 15px;
    }
    .left{
        /*padding: 50px 15px;*/
        width: 100%;


    }
    .right{
        padding: 50px 0;
        width: 100%;
        height: fit-content;
    }
    .title{
        width: 100%;
        font-size: 35px;
        font-weight: 600;
        line-height: 1.16;
        text-align: center;
    }
    .title span{
        color: var(--main-color);
    }
    .advantages{
        margin-top: 50px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        flex-wrap: wrap;
        gap: 30px;
        margin-bottom: 30px;
    }
    .icon{
        width: 50px;
        height: 50px;
        fill: var(--main-color);
    }
    .advantages p{
        font-size: 18px;
    }
    .advantages li{
        display: flex;
        align-items: center;
        gap: 30px;
    }
}

