/* "//-------------------------------------------------------DESKTOP----------------------------------------------------------------- */

@media (min-width: 1200px) {
    .form{
        position: relative;
        display: flex;
        justify-content: center;
    }
    .input{
        width: 280px;
        height: 60px;
        border: 1px solid #eaeaea;
        padding: 12px;
        padding-left: 20px;
        letter-spacing: 0.15em;
        font-size: 20px;
        margin-right: 20px;
    }
    .input:focus{
        border: 1px solid #eaeaea;
        outline: none;

    }
    .button{
        padding: 0 50px;
        height: 60px;
        background: var(--main-color);
        font-size: 20px;
        font-weight: 500;
        box-shadow: 0px 3px 0px 0px #414F4F;
        border-radius: 5px;
        color: white;
        transition: all 0.3s linear;
    }
    .button:hover{
        background: var(--black);
        transition: all 0.3s linear;
    }
    .error{
        position: absolute;
        font-size: 15px;
        color: #9b0c0c;
        bottom:-25px;
        left: 20px;

    }
}
