@media screen and (min-width: 320px) and (max-width: 767px) {
    .container {
        padding: 100px 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .title {
        font-size: 40px;
        font-weight: 600;
        line-height: 60px;
        color: var(--black);
        text-align: center;
    }

    .subTitle {
        color: var(--black);
        font-size: 30px;
        font-weight: 300;
        line-height: 39px;
        text-align: center;
        margin-bottom: 75px;
    }

    .list {
        display: flex;
        gap: 15px;
        justify-content: center;
        flex-direction: column;
        align-items: center;

    }

    .list li {
        width: 360px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .icon {
        width: 105px;
        height: 105px;
        margin-bottom: 15px;
    }

    .list h4 {
        font-size: 25px;
        font-weight: 600;
        color: var(--black);
        text-align: center;
    }

    .list p {
        text-align: center;
        font-size: 15px;
        color: var(--black);
    }
}


