@media screen and (min-width: 320px) and (max-width: 767px) {
    .callForm{
        width: 100%;
        display: flex;
        flex-direction: column;
        background-color: var(--bg-color);
        min-height: 300px;
        padding: 30px;
        padding-bottom: 50px;

    }
    .content{
        position: relative;
        width: 100%;

        display: flex;
        flex-direction: column;
        padding-bottom: 20px;
        margin-bottom: 50px;
    }
    .content:after{

        content: '';
        position: absolute;
        width: 100%;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
        height: 1px;
        background-color: #dadddf;
    }
    .left{
        /*width: 550px;*/
    }
    .image{
        /*width: 420px;*/
    }
    .title{
        font-size: 40px;
        font-weight: 300;
        line-height: 48px;
        color: var(--black);
    }
    .link{
        display: block;
        margin: 35px 0;
        font-size: 25px;
        font-weight: 800;
        color: var(--black);
        letter-spacing: 0.05em;
        text-align: center;

    }
}

